import { Range, Point } from '../../types';

const DEFAULT_POINT: Point = [-1, -1];

/**
 * 选区
 */
export class Selection {
    protected cell: Point;
    protected range: Range;

    /**
     * 选区起始点
     */
    private start: { x: number; y: number } = { x: 0, y: 0 };

    /**
     * 选区结束点
     */
    private end: { x: number; y: number } = { x: 0, y: 0 };

    private selectedTexts: string[][] = [];

    public constructor() {
        this.reset();
    }

    public selectCell(ri: number, ci: number, position: { x: number; y: number }): void {
        this.cell = [ci, ri];
        this.range = [DEFAULT_POINT, DEFAULT_POINT];
        this.start = position;
    }

    public selectRange(sr: number, sc: number, er: number, ec: number, position: { x: number; y: number }): void {
        if (this.cell === DEFAULT_POINT) {
            this.cell = [0, 0];
        }
        this.range = [
            [sc, sr],
            [ec, er],
        ];
        this.end = position;
    }

    public getCell(): Point {
        return this.cell;
    }

    public getRange(): Range {
        return [
            this.range[0] === DEFAULT_POINT ? this.cell : this.range[0],
            this.range[1] === DEFAULT_POINT ? this.cell : this.range[1],
        ];
    }

    public getPosition() {
        if (this.start.y > this.end.y) {
            return {
                start: this.end,
                end: this.start,
            };
        } else if (this.start.y === this.end.y && this.start.x > this.end.x) {
            return {
                start: this.end,
                end: this.start,
            };
        }

        return {
            start: this.start,
            end: this.end,
        };
    }

    public reset(): void {
        this.cell = DEFAULT_POINT;
        this.range = [DEFAULT_POINT, DEFAULT_POINT];
        this.start = { x: 0, y: 0 };
        this.end = { x: 0, y: 0 };
        this.setSelectedTexts([]);
    }

    public setSelectedTexts(texts: string[][]) {
        this.selectedTexts = texts;
    }

    public getSelectedTexts() {
        return this.selectedTexts;
    }
}
